
<div>
  <el-table
    ref="table"
    :data="list"
    height="500"
    row-key="id"
    tooltip-effect="dark"
    style="width: 100%"
    :highlight-selection-row="highlight"
    @select="onSelect"
    @select-all="onSelectAll"
    @selection-change="handleSelectionChange">
    <!-- 多选 -->
    <el-table-column width="60" type="selection" :selectable="getSelectable"></el-table-column>
    <el-table-column
      label="id"
      prop="id"
      width="120">
    </el-table-column>
    <el-table-column
      label="count"
      prop="count"
      width="120">
    </el-table-column>
    <el-table-column
      label="日期"
      width="120">
      <template slot-scope="scope">{{ scope.row.date }}</template>
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="120">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column label="操作" width="260">
      <template slot-scope="{ row }">
        <el-button @click="onDel(row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <div style="margin-top: 20px">
    <el-button @click="toggleSelection([list[1], list[2]])">切换第二、第三行的选中状态</el-button>
    <el-button @click="toggleSelection()">取消选择</el-button>
    <el-button @click="toggleAllSelection()">toggleAllSelection</el-button>
    <el-button @click="refresh()">刷新</el-button>
    <el-button @click="show = !show">show</el-button>
    <span style="margin-left: 10px;">选中高亮: <el-switch v-model="highlight"></el-switch></span>
  </div>
</div>
